import React, { useState, useCallback, useContext } from 'react';
import { StaticContext } from 'react-router';
import { Route, Redirect, RouteComponentProps } from 'react-router-dom';

import styles from './css/user.module.css';
import contactFields, { ContactMeta } from '../resources/contact';
import { UserSwitcher } from '../components/Switcher';
import { ProfileData, Team, ContactEntry } from '../interfaces';
import { FaAngleRight, FaCheckCircle, FaChess, FaUserCircle, FaCogs, FaUsers } from 'react-icons/fa';
import { isDev } from '../utils/requests';
import { flags } from '../config.json';
import countries from '../assets/countries.json';
import { useClub } from './callbacks/ChessCom';
import { ChessTeam } from 'chess-web-api';
import { capitalise } from '../utils/prototype';
import { LoginContext } from '../App';

export default function Info(props: RouteComponentProps<any, StaticContext, any> & {
	id?: string
	profile: Team & ProfileData
	updateProfile: () => void
	endSession?: () => Promise<void>
	isAuthed: boolean
}) {

	const [shouldFetch, fetchClub] = useState('');
	const chessComClub = useClub(shouldFetch) as ChessTeam;
	const getName = useCallback((meta: ContactMeta, entry: ContactEntry) => {
		if (entry.name) return entry.name;
		if (entry.username) return entry.username;
		if (entry.id) return entry.id;
		if (meta.id !== 'chessCom') return capitalise(meta.id as string);
		if (chessComClub.name) return chessComClub.name;
		if (entry.link && !shouldFetch) fetchClub(entry.link);
		return capitalise(meta.id);
	}, [fetchClub, chessComClub.name, shouldFetch]);

	const isLoggedIn = useContext(LoginContext);

	const [redirect, setRedirect] = useState(false);
	if (props.location.search.startsWith('?user=')) window.close();
	if (redirect) return <Redirect to='/' />;
	//console.log(props.profile);
	return (
		<div className={styles.container}>
			<div className={styles.title}>
				<div className={['container', styles.info].join(' ')}>
					<h1><span className='bold'>{props.profile.username || props.profile.name}</span></h1>						
					<div className={styles.details}>
						{props.profile.nationality ? <div className={styles.flag}>
							<img src={flags.replace('|', props.profile.nationality)} alt={props.profile.nationality}/>
							{countries[props.profile.nationality as keyof typeof countries]}
						</div> : null}
						<div>Account creation: {new Date(props.profile.createdAt).toString().slice(0, 15)}</div>{/*}
						<div>Tournaments: {tournaments.length}</div>
						<div>Public tournaments: {tournaments.filter(t => t.setting && t.setting.public).length}</div>*/}
					</div>
					<div className={styles.buttons}>
						{props.isAuthed ?
							<div className={styles.row}>								
								<a href={isDev ? 'https://localhost:3000' : 'https://beta.scorchapp.co.uk'} rel='noopener noreferrer' target='_blank' className={['button', 'link', styles.new].join(' ')}>
									Create New Tournament
									<FaAngleRight />
								</a>							
								{props.endSession ? <div className={['button', 'link', styles.logout].join(' ')} onClick={() => {
									if (props.endSession) props.endSession().then(() => setRedirect(true));
								}}>
									Log out
									<FaAngleRight />
								</div> : null}
							</div> :
						null}					
						{Object.entries(contactFields).reverse().map(([k, meta], i) => {
							if (!isLoggedIn) return null;
							meta = Object.assign(contactFields[k], { id: k }) as ContactMeta;
							if (!meta) return null;
							if (typeof meta !== 'object') console.log(meta);
							let entry =  props.profile[k as keyof typeof props.profile] as ContactEntry;
							if (k === 'email') entry = Object.assign(props.profile.emailMeta || {}, {id: props.profile.email}) as ContactEntry;
							if (!entry || typeof entry !== 'object' || (props.location.state.currentPath.startsWith('/@/') && !entry.verified)) return null;
							return (
								<div className={styles.row} key={['row', i].join('.')}>
									{entry.link || meta.idLink || meta.link ?
										<a
											id={k}
											href={entry.link || (meta.idLink || meta.link as string).replace('|', entry?.username || '')}
											className={['button', 'link', styles.account].join(' ')}
											rel='noopener noreferrer'
											target='_blank'
										>
											{typeof meta.icon[1] === 'string' ? <img src={meta.icon[1]} alt={meta.id} className={styles.icon} /> : React.cloneElement(meta.icon[1], { className: styles.icon })}
											{getName(meta, entry)}
											{entry.verified ? <FaCheckCircle className='verified' /> : null}
										</a> :
										<div
											id={k}
											className={['button', 'link', styles.account].join(' ')}
										>
											{typeof meta.icon[1] === 'string' ? <img src={meta.icon[1]} alt={meta.id} className={styles.icon} /> : React.cloneElement(meta.icon[1], { className: styles.icon })}
											{getName(meta, entry)}
											{entry.verified ? <FaCheckCircle className='verified' /> : null}
										</div>
									}
								</div>
							);
						})}
					</div>
					<Route path={`/team/${props.id}/:section?`} render={(rProps) => {
						return <UserSwitcher
							section={rProps.match.params.section}
							loaded
							sections={{
								tournaments: {
									link: `/@/${props.profile.username}/tournaments`,
									icon: <FaChess />
								},
								teams: {
									link: `/@/${props.profile.username}/teams`,
									icon: <FaUsers />
								},
								accounts: {
									hide: !props.isAuthed,
									link: `/@/${props.profile.username}/accounts`,
									icon: <FaUserCircle />
								},
								settings: {
									hide: !props.isAuthed,
									link: `/@/${props.profile.username}/settings`,
									icon: <FaCogs />
								}
							}}
						/>
					}} />
					<Route exact path={`/@/${props.profile.username}/:section?`} render={(rProps) => {
						return <UserSwitcher
							section={rProps.match.params.section}
							loaded
							sections={{
								tournaments: {
									link: `/@/${props.profile.username}/tournaments`,
									icon: <FaChess />
								},
								teams: {
									link: `/@/${props.profile.username}/teams`,
									icon: <FaUsers />
								},
								accounts: {
									hide: !props.isAuthed,
									link: `/@/${props.profile.username}/accounts`,
									icon: <FaUserCircle />
								},
								settings: {
									hide: !props.isAuthed,
									link: `/@/${props.profile.username}/settings`,
									icon: <FaCogs />
								}
							}}
						/>
					}} />
					
				</div>
			</div>
		</div>
	)
}